.clearfix:after,.clearfix:before{content:"";display:table}.clearfix:after{clear:both}.slider{height:600px;padding-top:85px;background:url(../img/sliderbg.jpg) top center no-repeat}.slider .col-md-6{padding:0}.slider .carousel-indicators{bottom:-50px}.slider .carousel-indicators li{background-color:#fff;border-color:#fff}.slider .carousel-indicators li.active{background-color:#bca51d;border-color:#bca51d}.slider .carousel-control{background:0 0}.slider .articles{width:100%;text-align:justify}.slider .articles article{display:inline-block;background-color:#00101c;min-height:380px;width:31%;padding:10px 10px 15px}.slider .articles article .news-title{height:40px;text-align:left;margin-top:20px;margin-bottom:20px}.slider .articles article .img-container{position:relative;height:160px;margin:0 auto;overflow:hidden;text-align:center;cursor:pointer}.slider .articles article .img-container:before{content:'';position:absolute;top:0;left:0;width:100%;height:100%;background-color:#bca51d;opacity:0;transition:all .2s ease-in}.slider .articles article .img-container .icon{position:absolute;top:35%;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%);font-size:3em;opacity:0;transition:all .2s ease-in}.slider .articles article .img-container:hover{transition:all .2s ease-in}.slider .articles article .img-container:hover:before{opacity:.3}.slider .articles article .img-container:hover .icon{opacity:1}.slider .articles .fill{width:100%;display:inline-block}@media screen and (max-width:992px){.slider .carousel-control{display:none}}